	module.exports = {"data":{"post":{"html":"<p>Advice to anyone upgrading a Debian box to potato remotely. Open a couple of extra ssh (you're not really using telnet to do this, are you?) sessions in case your main session gets stuck. I didn't, and I got burned! They don't call it unstable for nothing. Watch out for new config formats for bind and exim. </p>\n<p>Too many phone calls and visitors today! All 3 of us are pooped! And I get to start work again tomorrow. Oh joy. And school vacation is over, and there's road construction blocking one of the tubes of the Elbtunnel, so traffic will be even worse than usual for a Monday. Oh double joy.</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/01/crh0109.jpg\" height=\"300\" width=\"400\" border=\"0\" alt=\"crh0109.jpg: \" /></p>\n<p><a href=\"http://shanson.editthispage.com/stories/storyReader$74\">Reflections on childbirth</a> (still in progress): A follow-up to the article on <a href=\"http://shanson.editthispage.com/stories/storyReader$50\">Birthing Christopher</a></p>","fields":{"title":"Smiling at Mama","date":"09 Jan 2000","url":"https://papascott.de/2000-01-09-smiling-at-mama/"}}},"pathContext":{"slug":"/2000-01-09-smiling-at-mama/","prev":{"fields":{"title":"Reflections on childbirth","date":"09 Jan 2000","slug":"/2000-01-09-reflections-on-childbirth/"}},"next":{"fields":{"title":"E-mail from home","date":"11 Jan 2000","slug":"/2000-01-11-e-mail-from-home/"}}}}

